/* APP MAIN NAVIGATION */

.app-header {
  position: relative;
  padding: 15px 0px;

  display: flex;
  align-items: center;
  gap: 50px;
  justify-content: flex-start;
}

.app-header::after {
  content: '';
  position: absolute;

  height: 2px;
  width: -webkit-fill-available;

  background-color: aliceblue;

  bottom: 0px;
}

.app-headerNav {
  list-style: none;
  text-decoration: none;

  color: inherit;

  font-weight: inherit;
  font-size: inherit;
  font-family: inherit;
}

.app-headerNav.active {
  color: gold;
}

/* AUTH NAVIGATION */

.auth-navigation {
  display: flex;
  align-items: inherit;
  gap: 30px;

  color: aliceblue;
  font-size: inherit;

  list-style: none;
  margin: 0;
  padding: 0;
}

.auth-page {
  color: inherit;
  text-decoration: none;
}

.auth-page.active {
  color: gold;
}

/* USER NAVIGATION */

.user-text {
  color: gold;

  font-size: 20px;
  font-family: inherit;
  font-weight: 500;

  margin: 0;
  margin-left: 1000px;
}

.user-logout-button {
  cursor: pointer;

  border: 1px solid aliceblue;
  background-color: inherit;
  border-radius: 10px 0px 10px 0px;
  color: inherit;

  padding: 10px 20px;
}

.user-logout-button:hover {
  border: 1px solid gold;
  color: gold;
}
